import React from 'react';
import Footer from './Footer';
import { Chip } from '@mui/material';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import CurrencyInput from 'react-currency-input-field';

export default function PresentAgeRisk(props) {

	const [age, setAge] = React.useState("");

	const [loading, setLoading] = React.useState(true);
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const STATE = props.location.state

	const chipset = [
		{ "key": "25", "name": "25 years" },
		{ "key": "35", "name": "35 years" },
		{ "key": "40", "name": "40 years" }
	]

	React.useEffect(async ()=>{
		await db.get(db.model.riskProfile).then(p=>{
			if(p){
				setAge(p.age || "")
			} else {
				setAge("")
			}
		})
		setLoading(false)
	},[])

	const checkValidation = () => {
		if (age) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	},[age])

	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={9} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'6%'}}>
				{
					loading ?
					<ActivityLoaderMintbox />
						:
						<>
							<div className="bodyTextDark">What is your present age?</div>
								<CurrencyInput
									className='normalInput'
									id="age"
									name="age"
									placeholder="29 Years"
									value={age}
									allowDecimals={false}
									allowNegativeValue={false}
									maxLength={2}
									disableAbbreviations={true}
									autoFocus
									suffix=' Years'
									onValueChange={(value) => setAge(value)}
								/>
							<div className='hiddden-scrollbar'>
								{chipset.map((item) => {
									return (
										<Chip
											className={age===item.key?"normalChip selected":"normalChip"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setAge(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next={Number(age)>55?"/risk-profile/7":"/risk-profile/2"}
				state={{...STATE, age}}
				saveData = {{ age }} 
				buttonText="Confirm"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
